    height: 664px;
    padding: 32px 16px 0;
  }
}

.logo-icon {
  width: 199px;
  height: 50px;
}

.menu-button {
  stroke: var(--accent-color);
  z-index: 7;
  transition: transform 250ms var(--cubic);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 15px 20px;
}

.menu-button:hover, .menu-button:focus {
  transform: scale(1.2);
}

