  margin-left: auto;
  padding: 20px;
  display: block;
}

.close-button:hover, .close-button:focus {
  stroke: var(--hover-btn-primary);
}

.buy-button {
  background-color: var(--header-hero-text);
  transition: background-color 250ms var(--cubic);
  color: var(--accent-color);
