  .menu-container {
    width: 100vw;
    width: 250px;
    height: 100vh;
    z-index: 900;
    background-color: var(--mob-menu-color);
    visibility: hidden;
    transition: transform 250ms var(--cubic), visibility 250ms var(--cubic);
    padding: 60px 20px;
    position: absolute;
    top: 0;
    right: 0;
    overflow-y: scroll;
    transform: translateX(100%);
